import {SelectItem, SelectContainer, onItemSelected} from "../../ui/select";

export default function (nomenclature:any): SelectItem {

    return {
        value: nomenclature.id,
        text: nomenclature.name,
        isSelected: typeof nomenclature.selected === "boolean",
        metadata: nomenclature,
        element: null,
        createItem(instance:SelectContainer, item:SelectItem):HTMLElement {
            let option:HTMLDivElement = document.createElement('div');

            item.element = option;

            option.innerHTML = '<div>' + item.text + '</div><div class="item-description">' + item.metadata.name_for_department + '</div>';
            option.setAttribute('data-value', item.value);

            option.classList.add('select-option');

            option.addEventListener('click', onItemSelected.bind({
                instance: instance,
                current: item
            }));

            if (item.isSelected === true) {
                option.classList.add('active');
            }

            return option;
        }
    } as SelectItem;

}